/* App.css */
@page {
  size: A4;
  margin: 5mm;
}

body {
  font-family: "Noto Sans KR", sans-serif;
}

.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px 40px 20px 40px;
}

.App-header {
  display: flex;
  justify-content: space-between;
  /* margin-right: 20px; */
  align-items: center;
  /* margin-bottom: 20px; */
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
a,
li,
td,
th,
div {
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  font-weight: normal;
  color: inherit;
  line-height: normal;
  text-align: inherit;
  text-decoration: none;
  word-break: keep-all;
}

h1 {
  font-weight: 600;
  font-size: 36px;
  color: #333;
}

h2 {
  font-weight: 600;
  font-size: 28px;
  color: #333;
}

h3 {
  font-weight: 600;
  font-size: 24px;
  color: #333;
}

.container {
  /* border: 1px solid red; */
  /* background-color: green; */
  margin-bottom: 20px;
  padding-bottom: 20px;
  /* border-bottom: 1px solid #eee; */
}

.subtitle {
  /* background-color: #696969; */
  border-bottom: 2px solid #000000;
  margin-bottom: 16px;
  padding-bottom: 5px;
}

p {
  word-break: keep-all;
}
a:link {
  color: red;
}
a:visited {
  color: red;
}
a:hover {
  color: red;
}
a:active {
  color: red;
}
