.projects {
  margin-bottom: 20px;
}

.projects h2 {
  padding-bottom: 10px;
  margin-bottom: 20px;
  color: #333;
}

.projects p,
.projects ul,
.projects div {
  margin-bottom: 10px;
}

.projects-container {
  display: flex;
  gap: 20px;
  border-bottom: 1px solid #eee;
  margin-bottom: 16px;
  /* padding-bottom: 16px; */
}

.projects-link {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
  color: #0c59ff !important;
}
.projects-chips {
  margin-bottom: 8px;
}
.projects-item {
  /* position: sticky; */
  top: 16px;
  height: fit-content;
  flex: 1;
}

.projects-item h3 {
  font-size: 20px;
  font-weight: bold;
  margin: 0;
}

.projects-item p {
  font-size: 14px;
  margin: 5px 0;
  color: #666;
}

.projects-detail {
  margin-bottom: 30px !important;
}

.projects-details {
  flex: 3;
}

.projects-details h3 {
  font-size: 20px;
  font-weight: bold;
  margin: 0;
  color: #333;
}

.projects-details h4 {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
  color: #333;
}

.projects-details p {
  margin: 10px 0;
  color: #666;
}

.projects-details ul {
  margin: 10px 0;
  /* padding-left: 20px; */
}

.projects-details ul li {
  list-style-type: disc;
  margin: 5px 0;
  color: #666;
}

.projects-details .projects-detail:nth-child(n + 2) {
  border-top: 1px solid #eee;
  padding-top: 5px;
}
