.awards {
  margin-bottom: 20px;
}

.awards h2 {
  padding-bottom: 10px;
  margin-bottom: 20px;
  color: #333;
}

.awards p,
.awards ul,
.awards div {
  margin-bottom: 10px;
}

.award-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.award-header {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  margin-bottom: 8px;

  gap: 8px;
}
.award-title {
  font-size: 20px;
}
.award {
  display: flex;
  flex-direction: column;
}
.award p {
  color: #666;
}
