.experience {
  margin-bottom: 20px;
}

.experience h2 {
  padding-bottom: 10px;
  margin-bottom: 20px;
  color: #333;
}

.experience p,
.experience ul,
.experience div {
  margin-bottom: 10px;
}

.experience-container {
  display: flex;
  gap: 20px;
}

.experience-item {
  /* position: sticky; */
  top: 16px;
  height: fit-content;
  flex: 1;
}

.experience-item h3 {
  font-size: 20px;
  font-weight: bold;
  margin: 0;
}

.experience-item p {
  font-size: 14px;
  margin: 1px 0;
  color: #666;
}

.experience-detail {
  margin-bottom: 30px !important;
}

.experience-details {
  flex: 3;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.experience-details h3 {
  font-size: 20px;
  font-weight: bold;
  margin: 0;
  color: #333;
}

.experience-details h4 {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
  color: #333;
}

.experience-details p {
  margin: 10px 0;
  color: #666;
}

.experience-details ul {
  margin: 10px 0;
  /* padding-left: 20px; */
}

.experience-details ul li {
  list-style-type: disc;
  margin: 5px 0;
  color: #666;
}

.experience-details .experience-detail:nth-child(n + 2) {
  border-top: 1px solid #eee;
  padding-top: 5px;
}
